      <p>Â∞ÜÊôÆÈÄöÊï∞Â≠óÂíåÂ≠óÊØçËΩ¨Êç¢‰∏∫ÂêÑÁßçÁâπÊÆäÁ¨¶Âè∑Ê†ºÂºèÔºåÊîØÊåÅÂúÜÂúàÊï∞Â≠ó„ÄÅÊã¨Âè∑Êï∞Â≠ó„ÄÅÁΩóÈ©¨Êï∞Â≠óÁ≠â</p>
    </div>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="input-card">
          <div slot="header">
            <span>ËæìÂÖ•ÂÜÖÂÆπ</span>
            <div style="float: right;">
              <el-button size="mini" @click="loadSample">Á§∫‰æã</el-button>
              <el-button size="mini" @click="clearInput">Ê∏ÖÁ©∫</el-button>
            </div>
          </div>
          
          <el-input
            type="textarea"
            v-model="inputText"
            placeholder="ËØ∑ËæìÂÖ•Ë¶ÅËΩ¨Êç¢ÁöÑÊï∞Â≠óÊàñÂ≠óÊØçÔºåÂ¶ÇÔºö123abc"
            :rows="8"
            @input="convertText"
          ></el-input>
          
          <div class="input-stats">
            <span>Â≠óÁ¨¶Êï∞: {{ inputText.length }}</span>
            <span>Êï∞Â≠ó: {{ countNumbers(inputText) }}</span>
            <span>Â≠óÊØç: {{ countLetters(inputText) }}</span>
          </div>
        </el-card>

        <el-card class="options-card" style="margin-top: 20px;">
          <div slot="header">
            <span>ËΩ¨Êç¢ÈÄâÈ°π</span>
          </div>
          
          <el-form label-width="100px">
            <el-form-item label="ËΩ¨Êç¢Ê®°Âºè">
              <el-checkbox-group v-model="selectedModes" @change="convertText">

                <el-checkbox v-for="(value, key) in modeNames" :key="key" :label="key">{{ value }}</el-checkbox>

              </el-checkbox-group>
            </el-form-item>

            <el-form-item label="Â§ÑÁêÜÊñπÂºè">
              <el-radio-group v-model="processMode" @change="convertText">
                <el-radio label="replace">ÊõøÊç¢ÂéüÂ≠óÁ¨¶</el-radio>
                <el-radio label="append">ËøΩÂä†Âà∞ÂêéÈù¢</el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="‰øùÁïôÂéüÊñá">
              <el-switch v-model="keepOriginal" @change="convertText"></el-switch>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="result-card">
          <div slot="header">
            <span>ËΩ¨Êç¢ÁªìÊûú</span>
            <div style="float: right;">
              <el-button size="mini" @click="copyAllResults">
                <i class="el-icon-copy-document"></i> Â§çÂà∂ÂÖ®ÈÉ®
              </el-button>
              <el-button size="mini" @click="exportResults">
                <i class="el-icon-download"></i> ÂØºÂá∫
              </el-button>
            </div>
          </div>
          
          <div v-if="results.length === 0" class="no-result">
            <i class="el-icon-info"></i>
            <p>ËØ∑ËæìÂÖ•ÂÜÖÂÆπÂºÄÂßãËΩ¨Êç¢</p>
          </div>
          
          <div v-else class="result-list">
            <div v-for="result in results" :key="result.type" class="result-item">
              <div class="result-header">
                <span class="result-type">{{ result.name }}</span>
                <el-button size="mini" @click="copyResult(result.text)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </div>
              <div class="result-text">{{ result.text }}</div>
            </div>
          </div>
        </el-card>

        <el-card class="reference-card" style="margin-top: 20px;">
          <div slot="header">
            <span>Á¨¶Âè∑ÂèÇËÄÉ</span>
          </div>
          
          <el-tabs v-model="activeTab">
            <el-tab-pane label="ÂúÜÂúàÊï∞Â≠ó" name="circled">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.circled" :key="index" class="symbol-item">
                  {{ index + 1 }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
            
            <el-tab-pane label="Êã¨Âè∑Êï∞Â≠ó" name="parentheses">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.parentheses" :key="index" class="symbol-item">
                  {{ index + 1 }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
            
            <el-tab-pane label="Âè•ÁÇπÊï∞Â≠ó" name="period">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.period" :key="index" class="symbol-item">
                  {{ index + 1 }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
            
            <el-tab-pane label="ÁΩóÈ©¨Êï∞Â≠ó" name="roman">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.roman" :key="index" class="symbol-item">
                  {{ index + 1 }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
            
            <el-tab-pane label="ÂúÜÂúàÂ≠óÊØç" name="circled-letter">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.circledLetter" :key="index" class="symbol-item">
                  {{ String.fromCharCode(65 + index) }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
            
            <el-tab-pane label="Êã¨Âè∑Â≠óÊØç" name="parentheses-letter">
              <div class="symbol-grid">
                <span v-for="(symbol, index) in symbolMaps.parenthesesLetter" :key="index" class="symbol-item">
                  {{ String.fromCharCode(97 + index) }}: {{ symbol }}
                </span>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'SpecialCharView',
  metaInfo: {
    title: 'Â≠óÁ¨¶ËΩ¨ÁâπÊÆäÁ¨¶Âè∑ - Êï∞Â≠óÂ≠óÊØçÁâπÊÆäÁ¨¶Âè∑ËΩ¨Êç¢Â∑•ÂÖ∑',
    meta: [
      { name: 'description', content: 'Â∞ÜÊôÆÈÄöÊï∞Â≠óÂíåÂ≠óÊØçËΩ¨Êç¢‰∏∫ÂúÜÂúàÊï∞Â≠ó„ÄÅÊã¨Âè∑Êï∞Â≠ó„ÄÅÁΩóÈ©¨Êï∞Â≠óÁ≠âÁâπÊÆäÁ¨¶Âè∑Ê†ºÂºèÔºåÊîØÊåÅÊâπÈáèËΩ¨Êç¢„ÄÇ' },
      { name: 'keywords', content: 'ÁâπÊÆäÁ¨¶Âè∑,ÂúÜÂúàÊï∞Â≠ó,Êã¨Âè∑Êï∞Â≠ó,ÁΩóÈ©¨Êï∞Â≠ó,Â≠óÁ¨¶ËΩ¨Êç¢,Á¨¶Âè∑ËΩ¨Êç¢' }
    ]
  },
  data() {
    return {
      inputText: '',
      selectedModes: ['circled'],
      processMode: 'replace',
      keepOriginal: false,
      results: [],
      activeTab: 'circled',
      
      symbolMaps: {
        // chinese : ['Èõ∂', '‰∏Ä', '‰∫å', '‰∏â', 'Âõõ', '‰∫î', 'ÂÖ≠', '‰∏É', 'ÂÖ´', '‰πù', 'ÂçÅ'],
        // //Â£π„ÄÅË¥∞„ÄÅÂèÅ„ÄÅËÇÜ„ÄÅ‰ºç„ÄÅÈôÜ„ÄÅÊüí„ÄÅÊçå„ÄÅÁéñ„ÄÅÊãæ
        // //1Ô∏è‚É£ 2Ô∏è‚É£
        // chineseCapital : ['Èõ∂', 'Â£π', 'Ë¥∞', 'ÂèÅ', 'ËÇÜ', '‰ºç', 'ÈôÜ', 'Êüí', 'Êçå', 'Áéñ', 'Êãæ'],
        //
        // emojiNumbers : ['0Ô∏è‚É£', '1Ô∏è‚É£', '2Ô∏è‚É£', '3Ô∏è‚É£', '4Ô∏è‚É£', '5Ô∏è‚É£', '6Ô∏è‚É£', '7Ô∏è‚É£', '8Ô∏è‚É£', '9Ô∏è‚É£', 'üîü'],

        circled: ['‚ë†', '‚ë°', '‚ë¢', '‚ë£', '‚ë§', '‚ë•', '‚ë¶', '‚ëß', '‚ë®', '‚ë©', '‚ë™', '‚ë´', '‚ë¨', '‚ë≠', '‚ëÆ', '‚ëØ', '‚ë∞', '‚ë±', '‚ë≤', '‚ë≥'],
        parentheses: ['‚ë¥', '‚ëµ', '‚ë∂', '‚ë∑', '‚ë∏', '‚ëπ', '‚ë∫', '‚ëª', '‚ëº', '‚ëΩ', '‚ëæ', '‚ëø', '‚íÄ', '‚íÅ', '‚íÇ', '‚íÉ', '‚íÑ', '‚íÖ', '‚íÜ', '‚íá'],
        period: ['‚íà', '‚íâ', '‚íä', '‚íã', '‚íå', '‚íç', '‚íé', '‚íè', '‚íê', '‚íë', '‚íí', '‚íì', '‚íî', '‚íï', '‚íñ', '‚íó', '‚íò', '‚íô', '‚íö', '‚íõ'],
        roman: ['‚Ö†', '‚Ö°', '‚Ö¢', '‚Ö£', '‚Ö§', '‚Ö•', '‚Ö¶', '‚Öß', '‚Ö®', '‚Ö©', '‚Ö™', '‚Ö´'],
        circledLetter: ['‚í∂', '‚í∑', '‚í∏', '‚íπ', '‚í∫', '‚íª', '‚íº', '‚íΩ', '‚íæ', '‚íø', '‚ìÄ', '‚ìÅ', '‚ìÇ', '‚ìÉ', '‚ìÑ', '‚ìÖ', '‚ìÜ', '‚ìá', '‚ìà', '‚ìâ', '‚ìä', '‚ìã', '‚ìå', '‚ìç', '‚ìé', '‚ìè'],
        parenthesesLetter: ['‚íú', '‚íù', '‚íû', '‚íü', '‚í†', '‚í°', '‚í¢', '‚í£', '‚í§', '‚í•', '‚í¶', '‚íß', '‚í®', '‚í©', '‚í™', '‚í´', '‚í¨', '‚í≠', '‚íÆ', '‚íØ', '‚í∞', '‚í±', '‚í≤', '‚í≥', '‚í¥', '‚íµ']
      },
      
      modeNames: {
        // chinese: '‰∏≠Êñá',
        // chineseCapital: 'ÈáëÈ¢ùÊï∞Â≠ó',
        // emojiNumbers: 'emoji',
        circled: 'ÂúÜÂúàÊï∞Â≠ó',
        parentheses: 'Êã¨Âè∑Êï∞Â≠ó',
        period: 'Âè•ÁÇπÊï∞Â≠ó',
        roman: 'ÁΩóÈ©¨Êï∞Â≠ó',
        'circled-letter': 'ÂúÜÂúàÂ≠óÊØç',
        'parentheses-letter': 'Êã¨Âè∑Â≠óÊØç'
      }
    }
  },
  methods: {
    convertText() {
      if (!this.inputText.trim()) {
        this.results = []
        return
      }

      this.results = []
      
      this.selectedModes.forEach(mode => {
        let convertedText = this.inputText
        
        if (mode === 'circled') {
          convertedText = this.convertNumbers(convertedText, this.symbolMaps.circled)
        } else if (mode === 'parentheses') {
          convertedText = this.convertNumbers(convertedText, this.symbolMaps.parentheses)
        } else if (mode === 'period') {
          convertedText = this.convertNumbers(convertedText, this.symbolMaps.period)
        } else if (mode === 'roman') {
          convertedText = this.convertNumbers(convertedText, this.symbolMaps.roman)
        } else if (mode === 'circled-letter') {
          convertedText = this.convertLetters(convertedText, this.symbolMaps.circledLetter, true)
        } else if (mode === 'parentheses-letter') {
          convertedText = this.convertLetters(convertedText, this.symbolMaps.parenthesesLetter, false)
        }
        
        if (this.processMode === 'append') {
          convertedText = this.inputText + ' ‚Üí ' + convertedText
        }
        
        if (this.keepOriginal && this.processMode === 'replace') {
          convertedText = this.inputText + '\n' + convertedText
        }
        
        this.results.push({
          type: mode,
          name: this.modeNames[mode],
          text: convertedText
        })
      })
    },

    convertNumbers(text, symbolMap) {
      return text.replace(/\d/g, (match) => {
        const num = parseInt(match)
        if (num >= 1 && num <= symbolMap.length) {
          return symbolMap[num - 1]
        }
        return match
      })
    },

    convertLetters(text, symbolMap, isUppercase) {
      const pattern = isUppercase ? /[A-Z]/g : /[a-z]/g
      const baseCharCode = isUppercase ? 65 : 97
      
      return text.replace(pattern, (match) => {
        const index = match.charCodeAt(0) - baseCharCode
        if (index >= 0 && index < symbolMap.length) {
          return symbolMap[index]
        }
        return match
      })
    },

    countNumbers(text) {
      return (text.match(/\d/g) || []).length
    },

    countLetters(text) {
      return (text.match(/[a-zA-Z]/g) || []).length
    },

    loadSample() {
      this.inputText = '123ABC456def'
      this.selectedModes = ['circled', 'parentheses', 'circled-letter']
      this.convertText()
    },

    clearInput() {
      this.inputText = ''
      this.results = []
    },

    async copyResult(text) {
      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•: ' + error.message)
      }
    },

    async copyAllResults() {
      const allText = this.results.map(result => `${result.name}:\n${result.text}`).join('\n\n')
      try {
        const success = await ClipboardUtil.copyText(allText)
        if (success) {
          this.$message.success('Â∑≤Â§çÂà∂ÂÖ®ÈÉ®ÁªìÊûúÂà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•: ' + error.message)
      }
    },

    exportResults() {
      const content = this.results.map(result => `${result.name}:\n${result.text}`).join('\n\n')
      DownloadUtil.downloadText(content, 'ÁâπÊÆäÁ¨¶Âè∑ËΩ¨Êç¢ÁªìÊûú.txt')
    }
  }
}
</script>

